import './App.css';
import router from './router/router'
import Header from './compoments/Header/Header';
import { useRoutes } from 'react-router-dom'
import { Suspense, useState, useEffect } from 'react';
import { Modal, Checkbox } from 'antd';

function App() {
  const route = useRoutes(router)
  const [isModalOpen, setIsModalOpen] = useState(true);
  useEffect(() => {
    if (localStorage.getItem('dontShowModal')) {
      setIsModalOpen(false)
    }
  }, [])
  const changeCheckBox = (e) => {
    if (e.target.checked) {
      localStorage.setItem('dontShowModal', true)
    }
  }
  return (
    <div className="App">
      <Header></Header>
      <Suspense fallback={
        <div
          style={{
            textAlign: 'center',
            marginTop: 200
          }}
        >
          loading...
        </div>
      }>
        {route}
      </Suspense>
      <Modal footer={null} width='450px' title="" open={isModalOpen} onCancel={()=>{setIsModalOpen(false)}} closable={true}>
        <div className='modal-main'>
          <p>1.DEMOLOGO,</p>
          <p>2.DemoMetaMask,Goerli</p>
          <p>3.DEMOIPFS</p>
          <p>4.NFTDemoxxSkins</p>
          <p><Checkbox onChange={changeCheckBox}></Checkbox>  </p>
        </div>
      </Modal>
    </div>
  );
}

export default App;
